.background{
    background: linear-gradient(135deg, rgba(97,144,232,1) 0%, rgba(167,191,232,1) 100%);
    border-radius: 20px;
    width: 85vw;
    height: 85px;
    box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.25);
}

.tappable_left {
    opacity: 0.5%;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}

.tappable_center {
    opacity: 0.5%;
}

.tappable_right {
    opacity: 0.5%;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

.button {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 85px;
}